
"use client";

import Link from 'next/link';
import { Button } from "@/components/ui/button";
import {
  DropdownMenu,
  DropdownMenuCheckboxItem,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import type { Tag } from "@/types";
import { Menu as MenuIcon } from "lucide-react";

interface TagDropdownFilterProps {
  selectedTags: Set<Tag>;
  onSelectedTagsChange: (tag: Tag) => void;
}

// Updated menu options for filtering
const FILTER_OPTIONS: Tag[] = ["foro", "noticias", "espacios"];

export default function TagDropdownFilter({
  selectedTags,
  onSelectedTagsChange,
}: TagDropdownFilterProps) {
  const selectedCount = selectedTags.size;

  return (
    <DropdownMenu>
      <DropdownMenuTrigger asChild>
        <Button
          variant="destructive"
          className="hover:bg-accent hover:text-accent-foreground focus-visible:ring-destructive"
        >
          <MenuIcon className="mr-2 h-4 w-4" />
          Menu {selectedCount > 0 && `(${selectedCount})`}
        </Button>
      </DropdownMenuTrigger>
      <DropdownMenuContent className="w-56">
        <DropdownMenuLabel>Menú</DropdownMenuLabel>
        <DropdownMenuSeparator />
        {FILTER_OPTIONS.map((option) => (
          <DropdownMenuCheckboxItem
            key={option}
            checked={selectedTags.has(option)}
            onCheckedChange={() => onSelectedTagsChange(option)}
            onSelect={(event) => event.preventDefault()} // Prevent closing on item select
            className="hover:bg-accent hover:text-accent-foreground focus:bg-accent focus:text-accent-foreground"
          >
            {option.charAt(0).toUpperCase() + option.slice(1)} {/* Capitalize first letter */}
          </DropdownMenuCheckboxItem>
        ))}
        {/* This item is a direct link and does not affect filtering */}
        <DropdownMenuItem asChild className="pl-8 pr-2 py-1.5 hover:bg-accent hover:text-accent-foreground focus:bg-accent focus:text-accent-foreground cursor-pointer">
          <Link href="/prestamos">
            Préstamos
          </Link>
        </DropdownMenuItem>
      </DropdownMenuContent>
    </DropdownMenu>
  );
}
