
"use client";

import { useState, useCallback } from "react";
import Image from "next/image";
import type { Tag } from "@/types";
import AppShell from "@/components/layout/app-shell";
import ImageCarousel from "@/components/ui/image-carousel"; // Import the new carousel

export default function HomePage() {
  const [selectedTags, setSelectedTags] = useState<Set<Tag>>(new Set());

  const handleTagChange = useCallback((tag: Tag) => {
    setSelectedTags((prevTags) => {
      const newTags = new Set(prevTags);
      if (newTags.has(tag)) {
        newTags.delete(tag);
      } else {
        newTags.add(tag);
      }
      console.log("Selected menu options:", newTags);
      return newTags;
    });
  }, []);

  const carouselImages = [
    { src: 'https://placehold.co/1200x480.png', alt: 'Students engaged in sports activities', dataAiHint: 'students sports' },
    { src: 'https://placehold.co/1200x480.png', alt: 'Modern university campus building', dataAiHint: 'university campus' },
    { src: 'https://placehold.co/1200x480.png', alt: 'Group of diverse students studying together', dataAiHint: 'students study' },
    { src: 'https://placehold.co/1200x480.png', alt: 'Close-up of sports equipment', dataAiHint: 'sports equipment' },
  ];

  return (
    <AppShell selectedTags={selectedTags} onSelectedTagsChange={handleTagChange}>
      <div className="container mx-auto py-8 px-4"> {/* Added horizontal padding */}
        
        <ImageCarousel images={carouselImages} className="mb-8" />

        <div className="grid md:grid-cols-2 md:items-start gap-8">
          {/* Square 1: Quienes somos */}
          <div className="bg-primary text-primary-foreground p-6 rounded-lg shadow-lg">
            <div className="flex flex-col sm:flex-row items-start space-y-4 sm:space-y-0 sm:space-x-4">
              <div className="flex-shrink-0">
                <Image
                  src="https://placehold.co/120x120.png"
                  alt="Placeholder para Quienes somos"
                  width={120}
                  height={120}
                  className="rounded-md object-cover"
                  data-ai-hint="company team"
                />
              </div>
              <div className="flex-grow">
                <div className="bg-accent text-accent-foreground p-4 rounded-md shadow">
                  <h2 className="text-2xl font-semibold mb-2">
                    Quienes somos:
                  </h2>
                  <p className="text-sm">
                    somos un grupo de estudiantes que buscan fomentar el deporte dentro de la institucio. Buscamos conectar mas a los estudintes con los espacios y utencilios otorgados por la institucion de una manera facil, segura e intuitiba.
                  </p>
                </div>
              </div>
            </div>
          </div>

          {/* Square 2: ¿Cómo pedir un prestamo? */}
          <div className="bg-primary text-primary-foreground p-6 rounded-lg shadow-lg">
            <div className="flex flex-col sm:flex-row items-start space-y-4 sm:space-y-0 sm:space-x-4">
              <div className="flex-shrink-0">
                <Image
                  src="https://placehold.co/120x120.png"
                  alt="Placeholder para Cómo pedir un prestamo"
                  width={120}
                  height={120}
                  className="rounded-md object-cover"
                  data-ai-hint="financial planning"
                />
              </div>
              <div className="flex-grow">
                <div className="bg-accent text-accent-foreground p-4 rounded-md shadow">
                  <h2 className="text-2xl font-semibold mb-2">
                    ¿Cómo pedir un prestamo?:
                  </h2>
                  <p className="text-sm">
                    una vez hagas el inicio de sesión, accede al menu de la parte superior y haz click en el apartado de "utencilios", ahí podrás ver la disponibilidad, una vez hayas seleccionado lo que planeas prestar, llena los campos requeridos y reclama el utencilio con el token otrogado.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </AppShell>
  );
}
