
"use client";

import type { FC } from 'react';
import { useState, useEffect, useCallback } from 'react';
import Image from 'next/image';
import { Button } from '@/components/ui/button';
import { ChevronLeft, ChevronRight } from 'lucide-react';
import { cn } from '@/lib/utils';

interface CarouselImage {
  src: string;
  alt: string;
  dataAiHint?: string;
}

interface ImageCarouselProps {
  images: CarouselImage[];
  autoplay?: boolean;
  autoplayInterval?: number;
  className?: string;
}

const ImageCarousel: FC<ImageCarouselProps> = ({
  images,
  autoplay = true,
  autoplayInterval = 5000,
  className,
}) => {
  const [currentIndex, setCurrentIndex] = useState(0);

  const goToPrevious = useCallback(() => {
    const isFirstSlide = currentIndex === 0;
    const newIndex = isFirstSlide ? images.length - 1 : currentIndex - 1;
    setCurrentIndex(newIndex);
  }, [currentIndex, images.length]);

  const goToNext = useCallback(() => {
    const isLastSlide = currentIndex === images.length - 1;
    const newIndex = isLastSlide ? 0 : currentIndex + 1;
    setCurrentIndex(newIndex);
  }, [currentIndex, images.length]);

  useEffect(() => {
    if (!autoplay || images.length <= 1) return;

    const intervalId = setInterval(() => {
      goToNext();
    }, autoplayInterval);

    return () => clearInterval(intervalId);
  }, [autoplay, goToNext, images.length, autoplayInterval]);

  if (!images || images.length === 0) {
    return null;
  }

  return (
    <div className={cn("relative w-full h-64 md:h-80 lg:h-96 overflow-hidden rounded-lg shadow-lg", className)}>
      {images.map((image, index) => (
        <div
          key={index}
          className={cn(
            "absolute top-0 left-0 w-full h-full transition-opacity duration-1000 ease-in-out",
            index === currentIndex ? "opacity-100 z-10" : "opacity-0 z-0 pointer-events-none"
          )}
        >
          <Image
            src={image.src}
            alt={image.alt}
            layout="fill"
            objectFit="cover"
            className="rounded-lg"
            priority={index === 0} // Prioritize loading the first image
            data-ai-hint={image.dataAiHint || "carousel background"}
          />
        </div>
      ))}

      {images.length > 1 && (
        <>
          <Button
            variant="outline"
            size="icon"
            onClick={goToPrevious}
            className="absolute top-1/2 left-2 md:left-4 -translate-y-1/2 z-20 bg-background/60 hover:bg-background/90 text-foreground rounded-full p-2"
            aria-label="Previous image"
          >
            <ChevronLeft className="h-5 w-5 md:h-6 md:w-6" />
          </Button>
          <Button
            variant="outline"
            size="icon"
            onClick={goToNext}
            className="absolute top-1/2 right-2 md:right-4 -translate-y-1/2 z-20 bg-background/60 hover:bg-background/90 text-foreground rounded-full p-2"
            aria-label="Next image"
          >
            <ChevronRight className="h-5 w-5 md:h-6 md:w-6" />
          </Button>

          <div className="absolute bottom-3 md:bottom-4 left-1/2 -translate-x-1/2 z-20 flex space-x-2">
            {images.map((_, index) => (
              <button
                key={index}
                onClick={() => setCurrentIndex(index)}
                className={cn(
                  "h-2 w-2 md:h-2.5 md:w-2.5 rounded-full transition-all duration-300 ease-in-out",
                  index === currentIndex ? 'bg-primary scale-125' : 'bg-white/70 hover:bg-white'
                )}
                aria-label={`Go to image ${index + 1}`}
              />
            ))}
          </div>
        </>
      )}
    </div>
  );
};

export default ImageCarousel;
