
"use client";

import Image from 'next/image';
import Link from 'next/link';
import { Button } from '@/components/ui/button';
import type { Tag } from '@/types';
import TagDropdownFilter from '@/components/mockups/tag-dropdown-filter';
import { User, LogOut } from 'lucide-react';
import { useAuth } from '@/context/auth-context';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { useRouter } from "next/navigation";

interface AppHeaderProps {
  selectedTags: Set<Tag>;
  onSelectedTagsChange: (tag: Tag) => void;
}

export default function AppHeader({ selectedTags, onSelectedTagsChange }: AppHeaderProps) {
  const { user, logout } = useAuth();
  const router = useRouter();

  const handleLogout = () => {
    logout();
    router.push('/login');
  };

  return (
    <header className="sticky top-0 z-30 flex h-16 items-center justify-between gap-4 border-b bg-primary text-primary-foreground px-4 md:px-6 shadow-sm">
      <Link href="/" className="flex items-center gap-3" aria-label="Go to homepage">
        <Image src="/inem-logo.png" alt="InemSport Logo" width={40} height={40} className="h-10 w-10 object-contain" />
        <h1 className="text-xl font-semibold">InemSport</h1>
      </Link>
      <div className="flex items-center gap-2">
        <TagDropdownFilter
          selectedTags={selectedTags}
          onSelectedTagsChange={onSelectedTagsChange}
        />
        {user ? (
          <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <Button variant="ghost" size="icon" aria-label="Perfil">
                <User className="h-5 w-5" />
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent className="w-56" align="end">
              <DropdownMenuLabel>
                <p className="font-medium">{user.name}</p>
                <p className="text-xs text-muted-foreground">{user.email}</p>
              </DropdownMenuLabel>
              <DropdownMenuSeparator />
              <DropdownMenuItem onClick={handleLogout} className="cursor-pointer">
                <LogOut className="mr-2 h-4 w-4" />
                <span>Cerrar Sesión</span>
              </DropdownMenuItem>
            </DropdownMenuContent>
          </DropdownMenu>
        ) : (
          <Link href="/login" passHref>
            <Button variant="ghost" size="icon" aria-label="Iniciar Sesión">
              <User className="h-5 w-5" />
            </Button>
          </Link>
        )}
      </div>
    </header>
  );
}
